















import { Task } from "@/types";
import { defineComponent } from "@vue/composition-api";

export default defineComponent({
  data() {
    return {
      csv: "",
      message: "",
      separator: ",",
    };
  },
  methods: {
    close() {
      this.$emit("close");
    },
    submit() {
      this.$store
        .dispatch("importCSV", this.csv)
        .then((v) => {
          this.csv = "";
          this.message = "";
          this.$emit("close");
        })
        .catch((error) => {
          this.message = error;
        });
    },
  },
});
