








































import { defineComponent } from "@vue/composition-api";
export default defineComponent({
  computed: {
    drawer: {
      set(value: boolean) {
        this.$emit("input", value);
      },
      get(): boolean {
        return this.value;
      },
    },
    items() {
      return [
        {
          icon: "mdi-home",
          title: "Overview",
          link: "/",
        },
        {
          icon: "mdi-table",
          title: "Table",
          link: "/table",
        },
        {
          icon: "mdi-bulletin-board",
          title: "Kanban",
          link: "/kanban",
        },
        {
          icon: "mdi-chart-timeline",
          title: "Ganttchart",
          link: "/ganttchart",
        },
        {
          icon: "mdi-calendar",
          title: "Calendar",
          link: "/calendar",
        },
      ];
    },
    projects() {
      return ["Unnamed"];
    },
  },
  props: {
    value: Boolean,
  },
  emits: {
    input: (v: boolean) => true,
  },
});
